.container-checkout {
  border: 1px solid green;
  width: 100%;
  margin: auto;
  background-color: $color-grey;

  .div-checkout {
    border: 1px solid blue;
    margin: 88px auto 0;
    width: 1110px;
    height: 1200px;
  }

  .goBack {
    color: rgba($color: #000000, $alpha: 0.5);
    cursor: pointer;
    margin-bottom: 38px;
  }

  form {
    padding: 54px 48px 48px;
    width: 730px;
    height: 1126px;
    background-color: $color-white;
    border-radius: 8px;

    h3 {
      margin-bottom: 38px;
    }

    .div-info:nth-child(2) {
      margin-bottom: 53px;
    }

    .div-info:nth-child(3) {
      margin-bottom: 61px;
    }

    .div-info {
      width: 100%;
      height: auto;

      h6 {
        margin-bottom: 16px;
      }

      .info {
        display: flex;
        flex-flow: row wrap;
        column-gap: 16px;
        row-gap: 24px;
        width: 100%;
        height: auto;

        .section {
          width: auto;
        }

        label {
          display: block;
          margin-bottom: -4px;
        }
      }

      .div-radio:nth-child(2) {
        margin-bottom: 16px;
      }

      .div-radio {
        padding: 18px 16px;
        width: 309px;
        height: 56px;
        border: 1px solid #cfcfcf;
        border-radius: 8px;
        color: #000000;
        accent-color: $color-orange;

        label {
          margin-left: 16px;
        }
      }
    }
  }

  .input {
    border: 1px solid #cfcfcf;
    height: 56px;
    padding: 18px 24px;
    border-radius: 8px;

    &__small {
      width: 309px;
    }

    &__large {
      width: 634px;
    }
  }
}
